<template>
  <v-app app>
 <v-simple-table dense>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">
           
          </th>
          <th class="text-left">
         
         
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>Nome</td>
          <td>{{data.name}}</td>
        </tr>
        <tr>
          <td>Tamanho</td>
          <td>{{size}}</td>
        </tr>
        <tr>
          <td>Data criado </td>
          <td>{{ dateFormat }}</td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  


  </v-app>
</template>

<script>
  export default {
    name: 'Detail',
    props:{
      data:Object,
      desc:Object
    },
    computed:{

      dateFormat:function(){



        return new Date(this.desc.birthtime).toLocaleDateString('pt-br',{
          weekday:'long',
          month:'long',
          day:'2-digit',
          year:'numeric',
          
        })
      },
      size:function(){



        return (Number(this.desc.size)).toLocaleString('pt')
      }


    },

    mounted() {
      
    },
}
</script>
